import { useState, useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';//–æ–Ω–æ–≤–ª–µ–Ω–Ω—è –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ URL
import { fetchSearchResult } from 'services/services';
import Loader from 'components/Loader/Loader';// —ñ–º–ø–æ—Ä—Ç –ª–æ–∞–¥–µ—Ä–∞
import Form from 'components/Form/Form'; // —ñ–º–ø–æ—Ä—Ç —Ñ–æ—Ä–º–∏ 
import MoviesList from 'components/MoviesList/MoviesList';
import { MoviesBox } from './Movies.styled'; // —Å—Ç–∏–ª—ñ–∑–∞—Ü—ñ—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ñ–≤

const Movies = () => {
  const [movies, setMovies] = useState([]);
  const [searchParams, setSearchParams] = useSearchParams();
  const [loading, setLoading] = useState(false);
  const [requestFall, setRequestFall] = useState(false);
  const query = searchParams.get('query') ?? '';

  useEffect(() => {
    if (query === '') {
      setMovies([]);
      return;
     }
    setLoading(true);
    const answer = fetchSearchResult(query)
      .then(foundedMovies => {
        setMovies(foundedMovies);
        setRequestFall(foundedMovies.length === 0)
      })

      .catch(error => {
        console.error(error.message);
        // setRequestFall(error.message);
        setMovies([]);
      })
      .finally(() => {
        setLoading(false);
        // setRequestFall(false);
      });
      console.log(answer)
  }, [query]);
  

  const setParams = query => {
    const params = query !== '' ? { query } : {};
    setSearchParams(params);
    // console.log(params)
  };

  return (
    <MoviesBox>
      <Form setParams={setParams} />
      {loading && <Loader />}
      {requestFall && (<h1>No movies with this request. Please, try again</h1>) }
      {(query && movies.length > 0) && <h1>Found movies</h1>}
      {!!movies.length && <MoviesList movies={movies} />}
    </MoviesBox>
  );
};

export default Movies;

//*info - –ü–æ—à—É–∫ —Ñ—ñ–ª—å–º—ñ–≤ –ø–æ –Ω–∞–∑–≤—ñ –∑ –≤–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è–º –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ URL —ñ –≤–∏–≤–æ–¥—É —Ä–µ–∑—É–ª—å—Ç–∞—Ç—É –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω—Ü—ñ
/*
-----–Ø–∫ —Å—Ç–∞–ª–æ –ø—ñ—Å–ª—è –∑–º—ñ–Ω ---------
–•—É–∫ useState –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è —Å—Ç–∞–Ω—É:
    movies: –ú–∞—Å–∏–≤ —Ñ—ñ–ª—å–º—ñ–≤.
    searchParams: –ü–∞—Ä–∞–º–µ—Ç—Ä–∏ –ø–æ—à—É–∫–æ–≤–æ–≥–æ –∑–∞–ø–∏—Ç—É –∑ URL.
    loading: –°—Ç–∞–Ω –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –¥–∞–Ω–∏—Ö.
    requestFall: –°—Ç–∞–Ω, —è–∫–∏–π –ø–æ–∫–∞–∑—É—î, —á–∏ –±—É–≤ —Ö–∏–±–Ω–∏–π –∑–∞–ø–∏—Ç –Ω–∞ –ø–æ—à—É–∫.

[searchParams, setSearchParams] = useSearchParams(); - –∑—á–∏—Ç—É–≤–∞–Ω–Ω—è –∑–∞–ø–∏—Ç—É –∑ –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ URL,  useSearchParams - —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –∑–º—ñ–Ω–Ω–æ—ó query
useEffect, -  —Ä–µ–∞–≥—É—î –Ω–∞ –∑–º—ñ–Ω—É query, —è–∫—â–æ query –æ–±–Ω—É–ª—è—é—Ç—å—Å—è movies, –∑–∞–≤–µ—Ä—à—É—î—Ç—å—Å—è –≤–∏–∫–æ–Ω–∞–Ω–Ω—è, —ñ–Ω–∞–∫—à–µ –≤—Å—Ç–∞–Ω–æ–≤–ª—é—î—Ç—å—Å—è –≤–∏–∫–æ–Ω–∞–Ω–Ω—è loading - –Ω–∞ true
fetchSearchResult - –æ—Ç—Ä–∏–º–∞–Ω–Ω—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤ –ø–æ—à—É–∫—É —Ñ—ñ–ª—å–º—ñ–≤, 
setRequestFall(foundedMovies.length === 0) - –ø–µ—Ä–µ–≤—ñ—Ä—è—î—Ç—å—Å—è, —á–∏ –±—É–≤ –∑–∞–ø–∏—Ç –≤–¥–∞–ª–∏–º. –Ø–∫—â–æ –Ω—ñ—á–æ–≥–æ –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ, –≤—Å—Ç–∞–Ω–æ–≤–ª—é—î—Ç—å—Å—è requestFall –≤ true. 
–ø—Ä–∏ –ø–æ–º–∏–ª—Ü—ñ - –≤—Å—Ç–∞–Ω–æ–≤–ª—é—î—Ç—å—Å—è requestFall –≤ true, –≤–∏–≤–æ–¥–∏—Ç—å—Å—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ø–æ–º–∏–ª–∫—É {requestFall && (<h1>No ...)}
setParams -  –≤—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—è –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ URL –Ω–∞ –æ—Å–Ω–æ–≤—ñ –≤–≤–µ–¥–µ–Ω–æ–≥–æ –∑–∞–ø–∏—Ç—É. –Ø–∫—â–æ –∑–∞–ø–∏—Ç –ø–æ—Ä–æ–∂–Ω—ñ–π, –≤—Å—Ç–∞–Ω–æ–≤–ª—é—é—Ç—å—Å—è –ø–æ—Ä–æ–∂–Ω—ñ –ø–∞—Ä–∞–º–µ—Ç—Ä–∏.

return ()
<Form setParams={setParams} /> - —Ñ–æ—Ä–º–∏ –¥–ª—è –≤–≤–µ–¥–µ–Ω–Ω—è –ø–æ—à—É–∫–æ–≤–æ–≥–æ –∑–∞–ø–∏—Çe  setParams - –ø—Ä–æ–ø—Å
{loading && <Loader />} - —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è
{requestFall && (<h1>No ...)} - –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ø–æ–º–∏–ª–∫—É
{(query && movies.length > 0) && <h1>Found ...)} -—è–∫—â–æ query –Ω–µ –ø–æ—Ä–æ–∂–Ω—ñ–π —ñ –∫—ñ–ª—å–∫—ñ—Å—Ç—å —Ñ—ñ–ª—å–º—ñ–≤ –±—ñ–ª—å—à–µ 0 - –≤—ñ–¥–æ–±—Ä–∞–∂–∞—î–º–æ –∑–∞–≥–æ–ª–æ–≤–æ–∫ 
{!!movies.length && <MoviesList movies={movies} />} - –í—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —Å–ø–∏—Å–∫—É —Ñ—ñ–ª—å–º—ñ–≤

*/
