import { useLocation } from 'react-router-dom' // —Ö—É–∫ - —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ –ø–æ—Ç–æ—á–Ω—É URL-–∞–¥—Ä–µ—Å—É.
import PropTypes from 'prop-types';

import { List, Item, MovieLink } from './MoviesList.styled';


const MoviesList = ({ movies }) => {
  const location = useLocation(); // –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º–æ —Ö—É–∫ –¥–ª—è –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—ó –ø—Ä–æ –ø–æ—Ç–æ—á–Ω–∏–π URL

  return (
    <List> 
      {movies.length > 0 && 
      movies.map(({ id, title }) => {
        return (
          <Item key={id}>
            <MovieLink to={`/movies/${id}`} state={{ from: location }}>
            {title}
            </MovieLink>
          </Item>
        );
      })}
    </List>

//   <List>
//   {movies.map(movie => (
//     <Item key={movie.id}>
//       <MovieLink to={`/movies/${movie.id}`} state={{ from: location }}>
//         {movie.title}
//       </MovieLink>
//     </Item>
//   ))}
// </List>
  );
};

MoviesList.propTypes = {
  movies: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      id: PropTypes.number.isRequired,
    })
  ),
};

export default MoviesList;

