import axios from 'axios'; //–±—ñ–±–ª—ñ–æ—Ç–µ–∫–∞ –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–∏—Ö HTTP-–∑–∞–ø–∏—Ç—ñ–≤ –¥–æ API

axios.defaults.baseURL = 'https://api.themoviedb.org/3';
const API_KEY = '6cbea8cece37cf85eba0b83585dd8520';

// –°–ø–∏—Å–æ–∫ –ø–æ–ø—É–ª—è—Ä–Ω–∏—Ö —Ñ—ñ–ª—å–º—ñ–≤ –Ω–∞ –ø–æ—Ç–æ—á–Ω–∏–π –¥–µ–Ω—å
export const fetchTrending = async () => {
  const params = {
    api_key: API_KEY,
    page: 1, // –ø–∞–≥—ñ–Ω–∞—Ü—ñ—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤
  }; 
  try {const { data } = await axios.get('/trending/movie/day', { params });
  return data.results;
} catch (error){ 
  console.error(error);
}
};

//–ü–æ—à—É–∫ —Ñ—ñ–ª—å–º—ñ–º –ø–æ –∑–∞–ø–∏—Ç—É (query)
export const fetchSearchResult = async query => {
  const params = {
    api_key: API_KEY,
    page: 1,
    query,
  };
  const { data } = await axios.get('/search/movie', { params });
  console.log(data.results)
  return data.results.filter(movie =>
    movie.title.toLowerCase().includes(query.toLowerCase())
  );
};

//–Ü–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è –ø—Ä–æ –æ–±—Ä–∞–Ω–∏–π —Ñ—ñ–ª—å–º (movieId)
export const fetchOneMovie = async movieId => {
  const params = {
    api_key: API_KEY,
    page: 1,
  };
  const { data } = await axios.get(`/movie/${movieId}`, { params });
  // console.log(data);
  return data;
};

//–†–µ–∑—É–ª—å—Ç–∞—Ç –ø—Ä–æ –∞–∫—Ç–æ—Ä—Å—å–∫–∏–π —Å–∫–ª–∞–¥, —Ä–æ–ª—ñ (moviedId)
export const fetchCast = async movieId => {
  const params = {
    api_key: API_KEY,
    page: 1,
    language: 'en-US',
  };
   try{  
  const { data } = await axios.get(`/movie/${movieId}/credits`, { params });
  return data;
} catch (error) {
  console.error(error.message);
  throw new Error('Failed to fetch cast');
}
};

//–†–µ–∑—É–ª—å—Ç–∞—Ç –æ–≥–ª—è–¥—É —Ñ—ñ–ª—å–º—É (moviedId)
export const fetchReviews = async movieId => {
  const params = {
    api_key: API_KEY,
    page: 1,
    language: 'en-US',
  };
  const { data } = await axios.get(`/movie/${movieId}/reviews`, { params });
  console.log(data);
  return data.results;
  
};

